/**
 * #.# Widgets
 */

/**
 * #.# Button
 */

.widget .button {
	display: inline-block;
	background: #484744;
	border: none;
	color: #FFF;
	cursor: pointer;
	/* Improves usability and consistency of cursor style between image-type 'input' and others */
	text-decoration: none;
	font-size: 13px;
	padding: 12px 35px;
	-moz-border-radius: 3px;
	-webkit-border-radius: 3px;
	border-radius: 3px;
}

.button-container {
	&.align-center {
		text-align: center;
	}
	&.align-left {
		text-align: left;
	}
	&.align-right {
		text-align: right;
	}
	&.align-full .button {
		display: block;
		text-align: center;
	}
}

/**
 * #.# Call To Action
 */

.widget_call-to-action {
	padding: 25px 15px;
	border: 1px solid #EEEEEE;
	border-width: 1px 0;
	background: #F9F9F9;
	position: relative;
	.cta-headline {
		margin-bottom: 0;
		color: #444444;
	}
	.cta-sub-text {
		margin-top: 10px;
		margin-bottom: 0;
		color: #777777;
	}
	.button {
		position: absolute;
		top: 50%;
		right: 15px;
		margin-top: -22px;
		color: #F3F3F3;
	}
}

.content .widget_call-to-action .button {
	color: #F3F3F3;
}

@media screen and (max-width: 480px) {
	.widget_call-to-action .button {
		position: static;
		margin-top: 10px;
		display: block;
		text-align: center;
	}
}

/**
 * #.# Icon and Text
 */

.widget_icon-text {
	h3.widget-title {
		font-size: 15px;
		line-height: 1.5em;
		margin-bottom: 0.5em;
		a {
			color: inherit;
			text-decoration: none;
		}
	}
	.feature-icon {
		float: left;
		width: 32px;
		height: 32px;
		img {
			max-width: 100%;
			height: auto;
		}
	}
	.widget-text {
		margin-left: 45px;
	}
}

/**
 * #.# Headline Widget
 */

.widget_headline {
	.widget-title {
		span {
			display: block;
			line-height: 1.3em;
		}
		.size-medium {
			font-size: 130%;
		}
		.size-large {
			font-size: 160%;
		}
		.size-extra-large {
			font-size: 190%;
		}
	}
	.align-left {
		text-align: left;
	}
	.align-center {
		text-align: center;
	}
	.align-right {
		text-align: right;
	}
}

.widget img {
	max-width: 100%;
	height: auto;
}